:root {
  font-family: Arial, Helvetica, sans-serif;
}

.people {
  grid-template-columns: repeat(2, 200px);
  justify-content: center;
  gap: 30px 20px;
  padding: 30px;
  display: grid;
}

@media screen and (width >= 1000px) {
  .people {
    grid-template-columns: repeat(4, 200px);
  }
}

.person {
  box-sizing: border-box;
  border-radius: 10px;
  width: 200px;
  padding: 10px;
  transition: transform 1s linear;
  position: relative;
}

.person:hover {
  background-color: #eee;
  width: 220px;
  transform: scale(1.1);
}

.person:hover .details__extra {
  background-color: #eee;
  justify-content: center;
  align-items: center;
  gap: 10px;
  width: 100%;
  display: flex;
  transform: scale(1.1);
}

.person:hover .person__photo {
  border-radius: 60px;
}

.person__photo {
  width: 200px;
  height: 200px;
  transition: all 1;
  border-radius: 50%;
  margin-bottom: 8px;
  position: relative;
  overflow: hidden;
}

.person__photo img {
  object-fit: cover;
  object-position: center;
  width: 200px;
  height: 200px;
}

.person__rank {
  color: #8b0000;
  background-color: #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  font-size: 16px;
  font-weight: 700;
  display: flex;
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
}

.details {
  text-align: center;
  position: relative;
}

.details__name {
  color: #2f4f4f;
  margin-bottom: 9px;
  font-size: 20px;
  font-weight: 700;
  display: block;
}

.details__extra {
  border-bottom-right-radius: 10px;
  border-bottom-left-radius: 10px;
  width: 100%;
  font-size: 18px;
  font-weight: 400;
  display: none;
  position: absolute;
}
